@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400;500;600;700&display=swap');

$primaryBackground:rgba(255,255,255,1);
$primaryColor:rgba(0,0,0,1);

$secondaryBackground: rgba(0,0,0,0.85);
$secondaryColor:rgba(255,255,255,1);

body{
  background-color: $primaryBackground;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  margin: 0;
}
code {
  font-family: 'Fira Code', monospace;
  font-weight: 400;
}
.links{
  text-decoration: none;
  color: inherit;
}
main{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 90vh;
  .home{
    animation: fadeInUp 0.5s ease-in-out;
  }
  .main-info{
    font-size: 1rem;
    min-height: 70vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    line-height: 300%;
    .typewriter{
      font-size: 3rem;
    }
  }
  section{
    margin: 0rem 2rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    .quote{
      padding: 1rem;
      font-style: italic;
    }
    .author{
      font-weight: 500;
    }
  }
}
.logo{
  font-size: 2rem;
  position: fixed;
  left: 50%;
  top:2.5%;
  z-index: 1;
}
.footer{
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  ul{
    padding: 0;
    list-style: none;
    overflow: hidden;
    li{
      float: left;
      margin: 0rem 1rem;
    }
  }
  .nav-links{
    display: block;
    padding: 1rem;
    text-align: center;
  }
  .nav-links:hover{
    font-weight: 500;
  }
}
.header{
  position: fixed;
  top: 2.5%;
  left: 50%;
  transform: translate(-50%,0);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  a{
    img{
      width: 3rem;
      padding: 1rem;
      opacity: 0.75;
      cursor: pointer;
      transition: 0.2s all ease-in-out;
    }
    img:hover{
      opacity: 1;
    }
  }
}
.hello{
  display: flex;
  flex-direction: column;
  animation: fadeInUp 0.5s ease-in-out;
  span{
    text-align: center;
    margin-top: 1rem;
  }
  form{
    margin-top:3rem;
    .input-row{
      display: flex;
      flex-direction: column;
      margin-bottom: 1rem;
      .warning{
        color: red;
        font-size: 0.75rem;
        margin-left: 1rem;
      }
      label{
        margin-bottom: 0.5rem;
        font-size: 1rem;
      }
      input{
        height: fit-content;
        font-size: 0.8rem;
        padding: 0.5rem;
        border: 0.05rem solid rgba(0, 0, 0,1);
        outline: none;
      }
      textarea{
        resize: none;
        font-size: 0.8rem;
        padding: 0.5rem;
        border: 0.05rem solid rgba(0, 0, 0,1);
        outline: none;
        font-family: 'Roboto', sans-serif;
      }
    }
    button{
      padding: 0.5rem 1.25rem;
      cursor: pointer;
      border: 0.05rem solid rgba(0, 0, 0,1);
      background-color: $primaryBackground;
      font-family: 'Roboto', sans-serif;
      transition: 0.2s all ease-in-out;
    }
    button:hover{
      color: $secondaryColor;
      background-color: $secondaryBackground;
    }
  }
}
.projects{
  width: 90%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.project-item{
  width: 100%;
  animation: fadeInUp 0.5s ease-in-out;
  h4{
    text-align: center;
    cursor: pointer;
    opacity: 0.75;
    transition: 0.2s all ease-in-out;
  }
  section.visible{
    opacity: 1;
  }
  section{
    line-height: 180%;
    transition: 0.2s all ease-in-out;
    animation: fadeInUp 0.5s ease-in-out;
    .project-info{
      text-align: justify; 
    }
    .project-link{
      margin-top: 1rem;
      display: flex;
      align-items: center;
      justify-content: end;
      div{
        display: flex;
        align-items: center;
        justify-content: center;
      }
      .links{
        font-size: 0.9rem;
        font-style: italic;
      }
      .links:hover{
        text-decoration: underline;
      }
    }
  }
}
.active{
  font-weight: 600;
}
.project-item:hover{
  h4{
    opacity: 1;
  }
}
@keyframes fadeInUp {
  from {
    transform: translateY(20px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@media screen and (max-width:500px) {
  .footer{
    font-size: 0.85rem;
    ul{
      li{
        margin: 0rem 0.5rem;
      }
    }
  }
  .header{
    a{
      img{
        width: 1.5rem;
        padding: 0.75rem;
      }
    }
  }
  main{
    .main-info{
      font-size: 0.85rem;
      .typewriter{
        font-size: 2rem;
      }
    }
    section{
      font-size: 0.85rem;
    }
  }
  .project-item{
    width: 100%;
    font-size: 0.85rem;
    h4{
      opacity: 1;
    }
    section{
      .project-link{
        .links{
          font-size: 0.75rem;
        }
      }
    }
  }
}
.resume {
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: -1;
  background-color: antiquewhite;
}

@media screen and (min-width:500px) {
  .hello{
    width: 40vh;
  }
}
@media screen and (max-width:1400px) {
  .projects{
    flex-direction: column;
  }
}
@media screen and (max-width:1400px) and (min-width:768px) {
  .project-item{
    width: 75%;
  }
}